import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import propTypes from 'prop-types'

export class News extends Component { 
    static defualtProps={
        pageSize:10,
        country:"in"
    }
    static propTypes={
        pageSize:propTypes.number,
        country:propTypes.string
    }
    constructor(){
        super();
        this.state={
            articles:[],
            loading:true,
            page:1,
            totalResults:0
        }
    }
    async componentDidMount(){
         let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=07d70cc113b346e2a2b374f02e10e594&pageSize=${this.props.pageSize}`;
         this.setState({loading:true});
         let data=await fetch(url);
         let parsedData=await data.json();
         this.setState({articles:parsedData.articles,
            totalResults:parsedData.totalResults,
            loading:false
        });
    }
    handlePreviosClick=async()=>{
        console.log("Previous button clicked");
        let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=07d70cc113b346e2a2b374f02e10e594&page=${this.state.page-1}&pageSize=${this.props.pageSize}`;
        this.setState({loading:true});
        let data=await fetch(url);
        let parsedData=await data.json();
        this.setState({
            articles:parsedData.articles,
            page:this.state.page-1,
            totalResults:parsedData.totalResults,
            loading:false
        })
    }
    handleNextClick=async()=>{
        console.log("Next button clicked");
        let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=07d70cc113b346e2a2b374f02e10e594&page=${this.state.page+1}&pageSize=${this.props.pageSize}`;
        this.setState({loading:true});
        let data=await fetch(url);
        let parsedData=await data.json();
        this.setState({
            articles:parsedData.articles,
            page:this.state.page+1,
            totalResults:parsedData.totalResults,
            loading:false
        })
       }
    render() {
        return (
            <div className="container m-auto ">
            <h2 style={{textAlign:"center"}}>News Monkey-Top headlines</h2>
            {this.state.loading && <Spinner/>}
            <div className="container my-4">
            <div className="row">
            {   
                !this.state.loading &&
                this.state.articles.map((element)=>{
                return <div className="col-md-4 my-4" key={element.url} >
                <NewsItem title={element.title} description={element.description?element.description.slice(0,88):" "} imageUrl={element.urlToImage} url={element.url}/>
                </div>
            })}
             
            </div>
            <div className="container d-flex justify-content-between">
               <button type="button" disabled={this.state.page<=1} className="btn btn-primary" onClick={this.handlePreviosClick} >&larr; Previous</button>
               <button type="button" disabled={this.state.page+1>this.state.totalResults/this.props.pageSize} className="btn btn-primary" onClick={this.handleNextClick} >Next &rarr;</button>
            </div>
            </div>
            </div>
        )
    }
}

export default News

